.home h2 {
    font-family: 'Kaushan Script', cursive;
    font-size: 25px;
    color: #b31746;
    margin: 0.2rem;
}

.home h1 {
    font-family: 'RocknRoll One', sans-serif;
    font-size: 80px;
    color: whitesmoke;
    margin: 0.2rem;
}

.home h5 {
    font-size: 30px;
    color: whitesmoke;
    margin: 2rem;
}

.home button {
    background: transparent;
    outline:none;
    font-size: 25px;
    border-radius: 10px 10px 10px 10px;
    margin: 2rem;
    border: 2px solid #b31746;
    color: whitesmoke;
    padding: 10px;
}


.home button:hover {
    color: #b31746;
    border: 2px solid grey;
}

@media screen and (max-width: 600px) {
    .home {
        text-align: center;
    }
