.about-grid {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content:flex-start;
            justify-content:flex-start;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-color: white;
    /* height: 110vh; */

}

.profile-img {
    border-radius: 50%;
}

.about-grid h3 {
    font-weight: bold;
    font-size: 20px;
    margin: 1rem;
}

.about-grid p {
    color: grey;
    font-size: 18px;
    width:80%;
     margin-top: 0;
}

.grid-container {
    display: grid;
    grid-template-columns: auto auto;
    margin: auto;
}

.grid-item{
    font-weight: bold;
    font-size: 20px;
    margin: 1rem;
}


@media screen and (max-width: 600px) {
